.cart-items {
  background: #f6f9fc;
  height: 350px;
  padding: 50px 0;
}

.cart-items .cart-details {
  width: 70%;
}

.cart-items .cart-total {
  width: 30%;
  margin-top: 30px;
  margin-left: 30px;
  height: 130px;
}
.cart-list {
  background: white;
  margin-top: 30px;
}
.cart-items .img {
  width: 150px;
  height: 150px;
}
.cart-items img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.cart-items .cart-details h3 {
  font-size: 20px;
  font-weight: 500;
  margin-top: 20px;
}
.cart-items .cart-details h4 {
  font-size: 15px;
  font-weight: 400;
  margin-top: 50px;
  color: grey;
}
.cart-items h4 span {
  color: #e94560;
  margin-left: 230px ;
  font-weight: 500;
}
.removeCart {
  background: none;
  font-size: 15px;
  text-align: right;
  margin-right: -5px;
  margin-top: -20px;
}
.removeCart:hover{
  background: none;
  color: #e94560;
}
.cartControl {
  margin-top: 50px;
}
.cartControl button {
  width: 40px;
  height: 40px;
  margin: 10px;
  border-radius: 5px;
  font-size: 20px;
}
.incCart {
  background: none;
  border: 1px solid rgb(3 0 71 / 9%);
  color: #e94560;
}
.desCart {
  background: #f6f9fc;
}
.cart-total h4 {
  font-size: 15px;
  font-weight: 400;
}
.cart-total h3 {
  font-size: 20px;
  font-weight: 500;
  color: #e94560;
}
.cart-total h2 {
  font-size: 18px;
  margin-bottom: 20px;
  border-bottom: 1px solid rgb(3 0 71 / 9%);
  padding-bottom: 10px;
  color: #e94560;
}
.no-items {
  color: #e94560;
  font-size: 18px;
  margin-top: 30px;
  height: 130px;
}
.btn-cart button{
  width: 150px;
  height: 40px;
  margin-top: 32px;
  border-radius: 5px;
  font-size: 20px;
  background-color: green;
  margin-left: 130px;
  border: 1px solid rgb(3 0 71 / 9%);
  color: yellow;
}
.btn-cart p{
  font-size: 15px;
  margin-top: 80px;
  font-family: 'Times New Roman', Times, serif;
  color: black;
  font-style: italic;
  margin-left: -240px;
  text-decoration: underline;
}
